<template>
  <div class="padding-double">
    <mu-row gutter>
      <template v-for="item in articleMap">
        <mu-col width="100" desktop="33" tablet="50">
          <a :href="$route.params.type === 'blog' ? '#/article/'+item.path : item.path">
            <card-item :title="item.title" :time="item.time" :content="item.content"></card-item>
          </a>
        </mu-col>
      </template>
    </mu-row>
  </div>
</template>
<script>
import cardItem from '../components/card-item.vue'
import articleMap from '../map/articleMap.js'
import demoMap from '../map/demoMap.js'
export default {
  name: 'list',
  data () {
    return {
      articleMap
    }
  },
  components: {
    'card-item': cardItem
  },
  methods: {
    init () {
      console.log(this.$route.params.type)
      if (this.$route.params.type === 'demo') {
        this.articleMap = demoMap
      } else {
        this.articleMap = articleMap
      }
    }
  },
  created () {
    this.init()
  },
  watch: {
    '$route': 'init'
  }
}
</script>



